
// 仅仅为登陆注册的头部
.global-top-bar{
    // position: absolute;
    z-index: 9;
    top: 0px;
    left: 0px;
    width: 100%;
    background: #222222;
    // filter:progid:DXImageTransform.Microsoft.gradient(enabled='true',startColorstr='#66000000', endColorstr='#66000000');background:rgba(0,0,0,0.4);
    height: 60px;
    .lbox{
        padding-top: 6px;
        .logo-icon{
            background: url(../../images/logo-120x48.png) no-repeat;
            width: 120px;
            height: 48px;
            display: block;
        }
    }
    
    .rbox{
        font-size: 14px;
        .role{
            margin-top: 13px;
            margin-right: 10px;
            li{
                float: left;
                display: inline;
                margin-right: 20px;
                a{
                    display: inline-block;
                    width: 100px;
                    height: 34px;
                    color: #fff;
                    line-height: 34px;
                    border-radius: 30px;
                    text-align: center;
                    &:hover{
                        text-decoration: none;
                    }
                }
                .seller{
                    background: #fff;
                    color: #343539;
                    &:hover{
                        background: #aaaaaa;
                    }
                }
                .btn-pub{
                    .icon-pub{
                        width: 20px;
                        height: 20px;
                        background: url(../../images/icon-pub-20x20.png);
                        display: inline-block;
                        vertical-align: -5px;
                        margin-right: 5px;
                    }
                    background: #4092f1;
                    &:hover{
                        background: #66a8f4;
                    }
                }


            }
        }
        .login{
            padding: 20px 0 0 0 ;
            color: #fff;
            a{
                color: #fff;
            }
            .line{
                padding: 0px 10px;
            }
        }
    }
    
}


// 手机端头部处理
@media screen and (min-width: 320px) and (max-width: 480px)
{
    .global-top-bar{
        .top-bar{
            width : auto;
            padding-left: 10px;
            padding-right: 10px;
        }
        .lbox{
            padding-top: 4px;
            .logo-icon{
                width : 60px;
                height : 26px;
                background-size: cover ;
                margin-top: 13px;
            }
        }
        .rbox{
            font-size: 10px;
            margin-top: 3px;
            .role{
                margin-top: 18px;
                li{
                    margin-right: 5px;
                    a{
                        width: 70px;
                        height: 20px;
                        line-height: 20px;
                        font-size: 10px;
                    }
                }
            }

            .login{
                padding-top: 22px;
                .line{
                    padding: 0px 3px;
                }
            } 
            
        }

    }

    body{
        .footer{
            height:35px;
            .about{
                width : auto;
                padding-top: 10px;
                font-size: 10px;
            }
        }
    }
    
}



// 消费者pc版本的头部
.global-top-bar-consumers{
    // position: absolute;
    z-index: 9;
    top: 0px;
    left: 0px;
    width: 100%;
    background: #222222;
    // filter:progid:DXImageTransform.Microsoft.gradient(enabled='true',startColorstr='#66000000', endColorstr='#66000000');background:rgba(0,0,0,0.4);
    height: 60px;
    .lbox{
        padding-top: 6px;
        .logo-icon{
            background: url(../../images/logo-120x48.png) no-repeat;
            width: 120px;
            height: 48px;
            display: block;
        }
    }
    
    .rbox{
        font-size: 14px;
        .role{

        }
        .login{
            padding: 20px 0 0 0 ;
            color: #fff;
            margin-right: 20px;
            a{
                color: #eeeeee;

            }
            .line{
                padding: 0px 10px;
            }
        }
    }

    .header-pop-mod{
        padding: 8px 0 0 25px;
        .header-location-item{
            display: inline-block;
            position:relative;
            .ui-triangle{
                right: 15px;
                left:auto;
                top:14px;
                border-color: #666666 transparent transparent transparent;
                _border-color: #666666 #fff #fff #fff;
                _filter: chroma(color=white);
            }
            em.address{
                height:34px;
                line-height: 34px;
                padding: 0 40px;
                display:inline-block;
                border-radius: 15px;
                background:#fff;
                color: #666;
                position:relative;
                &:before{
                    content: "";
                    width:16px;
                    height:16px;
                    display: inline-block;
                    background:url(../../images/icon-location-16x16.png) no-repeat;
                    position:absolute;
                    left: 20px;
                    top: 50%;
                    margin-top: -8px;
                }

            }
        }
        .header-select-item{
            margin-top: 13px;
            position:relative;

            padding:20px 0px 0px;
            background:#fff;
            box-shadow: 1px 1px 5px #000;
            border-radius: 3px;
            .ui-triangle-top-max-white{
            position:absolute;
            left:15px;
            top:-4px;}
            .item-wrap{
                .item{
                    .title{
                        font-size: 14px;
                        color:#000;
                        padding:0px 18px 5px;
                    }
                    .list{
                        li{
                            height:34px;
                            line-height: 34px;
                            font-size: 14px;
                            a{
                                padding:0px 18px;
                                color:#666666;
                                text-decoration: none;
                                height:34px;
                                display:block;
                                &:hover{
                                    background:#eeeeee;
                                }
                            }

                        }
                        .cur a{
                            background:#eeeeee;
                        }   
                    }
                    .list-1{
                        li{
                            padding:0px 18px;
                            color:#4092f1;
                        }
                    }

                }
            }
        }
    }

    
}


